h3 {
  font-size: 20px;
  line-height: 140%;
  font-weight: 600;
}
.btn-yellow {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #FFE500;
  border-radius: 8px;
  &:hover {
    background: #FFED4D;
  }
  i {
    max-width: 20px;
    img {
      max-width: 100%;
    }
  }
  span {
    line-height: 150%;
    color: #21262E;
  }
}

