.header {
  position: relative;
  z-index: 10;
  .search-btn {
    display: flex;
    align-items: center;
    gap: 24px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #21262E;
      }
    }
    svg {
      max-width: 23px;
      width: auto;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
}
.header-top {
  background: #fff;
  border-radius: 12px 12px 0 0;
  padding: 0 0 16px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 24px;
  }
  &__logo {
    width: 107px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__phone,
  &__user {
    display: flex;
    align-items: center;
    gap: 8px;
    &:hover {
      span {
        color: #707680;
      }
    }
    i {
      max-width: 20px;
      display: flex;
      flex-shrink: 0;
      align-items: center;
      svg {
        max-width: 100%;
        width: auto;
        height: auto;
      }
    }
    span {
      font-size: 14px;
      line-height: 140%;
      transition: all 0.3s;
    }
  }
  @media screen and (min-width: 1024px) {
    .header-bottom__group {
      display: none;
    }
  }
}
.header-bottom {
  background: #fff;
  border-top: 1px solid #DCDFE5;
  border-bottom: 1px solid #DCDFE5;
  padding: 16px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__basket {
    display: flex;
    align-items: center;
    gap: 24px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #21262E;
      }
    }
    svg {
      max-width: 23px;
      width: auto;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__btn {
    height: 40px;
    padding: 5px 20px;
    span {
      font-size: 14px;
    }
  }
  .header-top__group {
    display: none;
  }
}
.menu {
  display: flex;
  align-items: center;
  gap: 24px;
  &-item {
    position: relative;
  }
  &-item2 {
    .menu-item__show {
      &:after {
        bottom: -25px;
        height: 25px;
      }
    }
  }
  &-item__show {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -10px;
      width: 100%;
      height: 10px;
      background: transparent;
    }
    i {
      width: 12px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &-item a {
    font-size: 16px;
    line-height: 150%;
    &:hover {
      color: #707680;
    }
  }
  .sub-menu {
    position: absolute;
    z-index: 5;
    top: 100%;
    left: 0;
    transform: translateY(-8px);
    width: 144px;
    background: #fff;
    box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
    border-radius: 8px;
    padding: 20px 16px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .sub-menu2 {
    position: fixed;
    top: 134px;
    width: 100%;
    box-shadow: 0px 12px 8px 0px rgba(34, 60, 80, 0.16);
    border-radius: 0 0 12px 12px;
    padding-top: 32px;
    padding-left: max(30px, calc(100% - 1200px) / 2);
    padding-right: max(30px, calc(100% - 1200px) / 2);
    padding-bottom: 0;
  }
  .sub-menu__wrap {
    height: 576px;
    padding-bottom: 32px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #A7ABB2;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #FFE500;
      border-radius: 3px;
    }
  }
  .sub-menu__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    a {
      font-size: 14px;
    }
  }
  &-item--active {
    .menu-item__show {
      color: #707680;
    }
    .sub-menu {
      transform: translateY(0);
      opacity: 1;
      pointer-events: all;
    }
    .menu-bg {
      opacity: 1 !important;
    }
  }
  .sub-menu__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    align-items: start;
    gap: 24px;
    margin-top: 24px;
  }
  .sub-menu__item {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    .sub-menu__list {
      gap: 8px;
    }
  }
  .sub-menu__item-group {
    display: flex;
    align-items: start;
    gap: 8px;
  }
  .sub-menu__item-icon {
    width: 56px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  .sub-menu__item-desc {
    @media screen and (min-width: 1024px) {
      .sub-menu {
        position: static;
        transform: unset;
        width: auto;
        background: transparent;
        box-shadow: unset;
        padding: 0;
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .sub-menu__item-title {
    font-size: 14px;
    line-height: 150%;
    font-weight: 600;
    margin-bottom: 8px;
    @media screen and (min-width: 1024px) {
      i {
        display: none;
      }
    }
  }
  .menu-bg {
    position: fixed;
    top: 135px;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.25);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .back {
    display: none;
    align-items: center;
    gap: 10px;
    font-weight: 600;
    color: #21262E;
    i {
      width: 18px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 14px;
    }
  }
}
.burger {
  position: relative;
  width: 18px;
  height: 13.5px;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #A7ABB2;
    border-radius: 5px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 6px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 5.5px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1100px) {
  .menu {
    gap: 20px;
  }
}

@media screen and (max-width: 1023px) {
  .header-top {
    border-bottom: 1px solid #DCE0E5;
    &__group {
      display: none;
    }
  }
  .header-bottom {
    position: fixed;
    top: 63px;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    border: none;
    padding: 12px 0 87px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #A7ABB2;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #FFE500;
      border-radius: 3px;
    }
    &__inner {
      flex-direction: column;
      align-items: start;
      gap: 16px;
    }
    .search-btn,
    &__basket {
      display: none;
    }
    .header-top__group {
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 16px;
    }
  }
  .menu {
    width: 100%;
    flex-direction: column;
    align-items: start;
    gap: 0;
    &-item {
      width: 100%;
      padding: 12px 0;
      &:not(:last-child) {
        border-bottom: 1px solid #DCE0E5;
      }
      a {
        width: 100%;
        display: flex;
      }
    }
    &-item--active {
      .sub-menu {
        left: 0 !important;
      }
    }
    .menu-item__show {
      justify-content: space-between;
      &:after {
        display: none;
      }
      i {
        transform: rotate(-90deg);
      }
    }
    .sub-menu {
      position: fixed;
      top: 63px;
      left: -105%;
      transform: unset;
      width: 100%;
      height: 100%;
      box-shadow: unset;
      padding: 12px 30px 93px;
      opacity: 1;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #A7ABB2;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #FFE500;
        border-radius: 3px;
      }
    }
    .sub-menu2 {
      height: 100%;
      .sub-menu {
        left: -105% !important;
      }
      .back {
        border-bottom: 1px solid #DCE0E5;
      }
    }
    .sub-menu__wrap {
      height: auto;
      padding-bottom: 0;
      overflow: unset;
    }
    .back {
      display: flex;
      padding: 12px 0;
    }
    .sub-menu__list {
      gap: 12px;
    }
    .menu-bg {
      display: none;
    }
    h3 {
      display: none;
    }
    .sub-menu__items {
      grid-template-columns: 100%;
      gap: 0;
      margin-top: 0;
    }
    .sub-menu__item {
      gap: 0;
      &:last-child {
        .sub-menu__item-group:last-child .sub-menu__item-desc {
          border: unset;
        }
      }
      .sub-menu__list {
        display: block;
        .back {
          border-bottom: unset;
          padding: 0;
        }
      }
      .sub-menu__list li {
        border-bottom: 1px solid #DCE0E5;
        padding: 12px 0;
        &:last-child {
          border: none;
        }
      }
    }
    .sub-menu__item-desc {
      border-bottom: 1px solid #DCE0E5;

    }
    .sub-menu__item-group .menu-item--active {
      .sub-menu {
        left: 0 !important;
      }
    }
    .sub-menu__item-icon {
      display: none;
    }
    .sub-menu__item-title {
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .menu {
    .sub-menu {
      padding: 12px 20px 93px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__logo {
      width: 96px;
    }
    &__group {
      width: 100%;
    }
    &__phone,
    &__user {
      width: 100%;
      height: 48px;
      justify-content: center;
      border: 1px solid #A7ABB2;
      border-radius: 8px;
      padding: 5px 16px;
    }
    .header-bottom__group {
      width: auto;
    }
  }
  .header-bottom {
    top: 58px;
    &__group {
      width: 100%;
    }
    &__btn {
      width: 100%;
      height: 48px;
    }
  }
  .menu {
    &-item a {
      font-size: 14px;
    }
    .sub-menu {
      top: 58px;
    }
  }
}